import React from "react";

import Signoff from "../../Signoff";

const Page = props => (
  <div>
    <h2>What Do I Do When I Complete Personal Progress?</h2>

    <p>
      Completing the Personal Progress program is a significant step in
      strengthening your faith and testimony and preparing for temple covenants.
      You have learned how to make commitments, carry them out, and report your
      progress. This process should continue as you remain on the path to the
      temple and continue to develop qualities that will help you throughout
      your life. You may continue your progress by earning an Honor Bee or by
      beginning Personal Progress again.
    </p>

    <h4>Earn an Honor Bee</h4>
    <p>
      The Honor Bee charm was historically given to young women who were willing
      to do more than was required. To continue your progress, you may earn an
      Honor Bee to accompany your Young Womanhood Recognition medallion. You may
      earn an Honor Bee charm after completing both of the following
      requirements:
    </p>

    <ol>
      <li>
        Read the <a href="https://lds.org/scriptures/bofm/">Book of Mormon</a>{" "}
        again. A daily habit of scripture study will strengthen your faith and
        help you receive personal inspiration and guidance throughout your life.
        If you read just five minutes a day, you can complete the entire Book of
        Mormon in one year.
        <Signoff id="honorBeeSignoff1" />
      </li>

      <li>
        Serve others for a total of 40 hours. Where possible, some of these
        hours should include mentoring another young woman with her Personal
        Progress. This should be done under the direction of the ward or branch
        Young Women presidency. As you help another young woman, you may want
        to:
        <ol>
          <li>Be her friend.</li>
          <li>Be an example of a righteous daughter of God.</li>
          <li>
            Encourage her to work with her parents in selecting appropriate
            value experiences and projects.
          </li>
          <li>Help her follow through with value experiences and projects.</li>
          <li>Help her keep records of value experiences and projects.</li>
          <li>
            Help her understand the gospel principles that the value experiences
            and projects teach.
          </li>
          <li>
            Encourage her to read the{" "}
            <a href="https://lds.org/scriptures/bofm/">Book of Mormon</a>, and
            discuss with her what she is reading.
          </li>
          <li>
            Encourage her to share value experiences and projects with her
            family.
          </li>
        </ol>
        <Signoff id="goodWorksSignoff2" />
      </li>
    </ol>

    <h4>Begin Personal Progress Again</h4>
    <p>
      You may begin the entire Personal Progress program again and earn a second
      Young Womanhood Recognition medallion.
    </p>
  </div>
);

export default Page;



